.mainModal {
    margin: 12px 20px 0px 20px;
    background-color: white;
    border: 1px solid #e6e6e6;
    border-radius: 5px;
    overflow: hidden;
    transition: height 0.3s ease;
}

.mainModal.expanded {
    height: 106px;
}

.mainModal.collapsed {
    height: 43px;
}

.mainHeader {
    height: 43px;
    display: flex;
    justify-content: space-between;
    border-bottom: 2px solid #e0e0e0;
    clip-path: polygon(0 0, 100% 0, calc(100% - 16px) 100%, 0 100%);
}

.sampleAndBarcode {
    display: flex;
}

.sampleCollectionHeading,
.barCodeHeading {
    padding: 0px 16px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    gap: 5px;
    height: 100%;
    cursor: pointer;
}

.sampleCollectionHeading:hover,
.barCodeHeading:hover {
    background: #f0f8ff;
}

.sampleCollectionHeading.selected,
.barCodeHeading.selected {
    background: #d3e6f2;
    border-bottom: 2px solid #2f8bc3;
    font-weight: bold;
}

.collapses {
    padding-right: 16px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-items: center;
    gap: 2px;
    font-size: 11px;
    cursor: pointer;
}

.mainModalContent {
    height: calc(100% - 43px);
}

.pinText {
    font-size: 14px;
    font-weight: 500;
}

.sampleName {
    font-weight: 16px;
    font-weight: 600;
}

.searchB {
    font-size: 14px;
    font-weight: 500;
    color: #2f8bc3;
    border: 1px solid #2f8bc3;
    border-radius: 5px;
    background-color: white;
    padding: 5px 15px;
}

.searchB:hover {
    font-size: 14px;
    font-weight: 500;
    color: #2f8bc3;
    border: 1px solid #2f8bc3;
    border-radius: 5px;
    background-color: white;
    padding: 5px 15px;
}

.input-group1 {
    border: 1px solid #ddd;
    border-radius: 5px;
    padding: 5px 15px;
}

.input-group1:focus-visible {
    border: 1px solid #ddd;
    box-shadow: none;
    outline: none;
}

.errorMessage {
    margin: 12px 20px 0px 20px;
}

.errorStyle1 {
    background-color: #ecc5c6;
    border-radius: 5px;
}

.errorStyle2 {
    background-color: #ebcfb1;
    border-radius: 5px;
}

.errorStyle3 {
    background-color: #c4d4e6;
    border-radius: 5px;
}

.errorText {
    font-size: 13px;
    font-weight: 500;
}

.defaultErrorStyle {
    background-color: #e2e3e5;
    color: #383d41;
    border: 1px solid #d6d8db;
}

.table thead tr th {
    background: #adcde1;
    font-size: 16px;
    font-weight: 600;
}

.table tbody tr td {
    font-size: 15px;
    font-weight: 400;
}

.form-check .form-check-input {
    margin-left: -1.25em;
}

.form-check-input {
    margin-top: 0.45em;
}

.widget {
    height: 100%;
    border-radius: 5px;
    background-color: #fff;
}

.labelText {
    color: #797979;
    font-size: 12px;
    font-weight: 400;
}

.key {
    font-size: 12px;
    font-weight: 400;
    color: #8e8e8e;
}

.value {
    font-size: 15px;
    font-weight: 600;
    color: #000;
}

.values {
    font-size: 15px;
    font-weight: 400;
    color: #000;
}

.overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 9999;
    display: flex;
    align-items: center;
    justify-content: center;
    pointer-events: auto;
}

.loader {
    width: 50px;
    height: 50px;
    border: 6px solid #ccc;
    border-top-color: #3498db;
    border-radius: 50%;
    animation: spin 1s linear infinite;
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}

.saveB {
    font-size: 14px;
    font-weight: 500;
    color: #fff;
    background-color: #2f8bc3;
    border-radius: 5px;
    padding: 5px 15px;
}

.updateB{
    font-size: 14px;
    font-weight: 500;
    color: #fff;
    background-color: #2f8bc3;
    border-radius: 5px;
    padding: 5px 15px;
}

.cancelB {
    font-size: 14px;
    font-weight: 500;
    color: rgb(219 52 67);
    border-radius: 5px;
    border: 1px solid rgb(219 52 67);
    padding: 5px 15px;
}

.saveB:hover {
    border: 1px solid #2f8bc3;
    color: #2f7bc3;
}

.updateB:hover {
    border: 1px solid #2f8bc3;
    color: #2f7bc3;
}

.cancelB:hover {
    border: 1px solid rgb(219 52 67);
    color: rgb(219 52 67);
}


/* ---------------- ayan css ---------------- */
.searchContents {
    height: 100%;
    font-family: 'Source Sans 3'
}

.searchFieldsAndButton {
    height: 100%;
    padding: 0 16px;
    display: flex;
    align-items: center;
    gap: 20px;
    flex-wrap: wrap;
}

.collectionArea,
.numberField {
    gap: 8px;
    align-items: center;
    height: 100%;
}

.collectionAreaLabel,
.numberFieldLabel {
    font-size: 15px;
}

.NumberInput {
    display: flex;
    flex-direction: column;
    position: relative;
}

.pinOrMobileError {
    color: red;
    font-size: 14px;
    position: absolute;
    top: 27px;
}

.searchB:hover {
    background: #2f8bc3;
    color: white;
}

/* ---------- end ayan css----------- */


@media (max-width: 768px) {

    .mainModal.expanded {
        height: 170px;
    }
}